@keyframes backRight {
  0% {
      -webkit-transform: translateX(200px) scale(.7);
      transform: translateX(200px) scale(.7);
      opacity: .7
  }

  80% {
      -webkit-transform: translateX(0) scale(.7);
      transform: translateX(0) scale(.7);
      opacity: .7
  }

  to {
      -webkit-transform: scale(1);
      transform: scale(1);
      opacity: 1
  }
}

