/* card front back */

.card-container {
  position: relative;
  width: 300px;
  height: 380px;
  top: 30%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  perspective: 1000px;
}
.card-container .left {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  z-index: 2;
}
.card-container .left:hover {
  width: 100%;
  z-index: 3;
}
.card-container .right {
  position: absolute;
  top: 0;
  right: 0;
  width: 50%;
  height: 100%;
  z-index: 2;
}
.card-container .right:hover {
  width: 100%;
  z-index: 3;
}
.card-container .cardfb {
  position: absolute;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform 0.5s ease-in-out;
}
.card-container .left:hover ~ .cardfb {
  transform: rotateY(-180deg);
}
.card-container .right:hover ~ .cardfb {
  transform: rotateY(180deg);
}

.cardfb .front, .cardfb .back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-family: "Roboto", sans-serif;
  color: #444;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 25px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}
.cardfb .front {
  background: #fff;
  z-index: 2;
}
.cardfb .back {
  background: #466599;
  z-index: 3;
  transform: rotateY(180deg);
}
.cardfb .front .cover {
  width: 100%;
  height: 150px;
  background: url("https://image.ibb.co/jbG1LQ/pexels_photo_167276.jpg") center -220px;
  border-radius: 25px 25px 0 0;
}
.cardfb .front .photo {
  margin-top: -45px;
  position: relative;
}
.cardfb .front .photo img {
  display: block;
  margin: auto;
  width: 100px;
  height: 100px;
  object-fit: cover; /* ใช้เพื่อปรับเปลี่ยนอัตราส่วนภาพ */
  object-position: 0 0;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
.cardfb .front .name {
  margin: 10px auto 5px;
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1;
  text-align: center;
}
.cardfb .front .role {
  font-size: 1rem;
  margin: 0;
  text-align: center;
  color: #999;
}
.cardfb .front .supporting_text {
  font-size: 1rem;
  margin: 15px auto 20px;
  padding: 8px;
  text-align: center;
}
.cardfb .back {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.cardfb .back .photo {
  margin-top: -45px;
}
.cardfb .back .photo img {
  position: relative;
  display: block;
  margin: auto;
  width: 100%;
  height: 100%;
  top: 20px;
}



